'use client';

import { useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { ChevronDownIcon, ShieldCheckIcon, BellAlertIcon, LanguageIcon, MoonIcon, UserCircleIcon } from '@heroicons/react/24/outline';

export default function SettingsPage() {
  const { user, loading } = useAuth();
  const [activeTab, setActiveTab] = useState('account');
  
  const [accountSettings, setAccountSettings] = useState({
    email: 'user@example.com',
    language: 'ar',
    notifications: true,
    twoFactorAuth: false,
    darkMode: false,
    newsletter: true,
    publicProfile: true,
  });

  const handleSettingChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value, type } = e.target as HTMLInputElement; // Type assertion
    const newValue = type === 'checkbox' ? (e.target as HTMLInputElement).checked : value;
    
    setAccountSettings({
      ...accountSettings,
      [name]: newValue
    });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // في التطبيق الحقيقي هنا سيتم إرسال الإعدادات الجديدة إلى الخادم
    console.log('تم حفظ الإعدادات:', accountSettings);
    alert('تم حفظ الإعدادات بنجاح!');
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center h-96">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto">
      <h1 className="text-2xl font-bold text-gray-900 mb-6">إعدادات الحساب</h1>
      
      <div className="bg-white shadow rounded-lg overflow-hidden">
        <div className="flex border-b border-gray-200">
          <button
            onClick={() => setActiveTab('account')}
            className={`px-4 py-4 text-sm font-medium ${
              activeTab === 'account'
                ? 'text-indigo-600 border-b-2 border-indigo-600'
                : 'text-gray-500 hover:text-gray-700 hover:border-gray-300'
            }`}
          >
            <div className="flex items-center">
              <UserCircleIcon className="h-5 w-5 ml-1" />
              الحساب
            </div>
          </button>
          
          <button
            onClick={() => setActiveTab('security')}
            className={`px-4 py-4 text-sm font-medium ${
              activeTab === 'security'
                ? 'text-indigo-600 border-b-2 border-indigo-600'
                : 'text-gray-500 hover:text-gray-700 hover:border-gray-300'
            }`}
          >
            <div className="flex items-center">
              <ShieldCheckIcon className="h-5 w-5 ml-1" />
              الأمان
            </div>
          </button>
          
          <button
            onClick={() => setActiveTab('notifications')}
            className={`px-4 py-4 text-sm font-medium ${
              activeTab === 'notifications'
                ? 'text-indigo-600 border-b-2 border-indigo-600'
                : 'text-gray-500 hover:text-gray-700 hover:border-gray-300'
            }`}
          >
            <div className="flex items-center">
              <BellAlertIcon className="h-5 w-5 ml-1" />
              الإشعارات
            </div>
          </button>
          
          <button
            onClick={() => setActiveTab('appearance')}
            className={`px-4 py-4 text-sm font-medium ${
              activeTab === 'appearance'
                ? 'text-indigo-600 border-b-2 border-indigo-600'
                : 'text-gray-500 hover:text-gray-700 hover:border-gray-300'
            }`}
          >
            <div className="flex items-center">
              <MoonIcon className="h-5 w-5 ml-1" />
              المظهر
            </div>
          </button>
          
          <button
            onClick={() => setActiveTab('language')}
            className={`px-4 py-4 text-sm font-medium ${
              activeTab === 'language'
                ? 'text-indigo-600 border-b-2 border-indigo-600'
                : 'text-gray-500 hover:text-gray-700 hover:border-gray-300'
            }`}
          >
            <div className="flex items-center">
              <LanguageIcon className="h-5 w-5 ml-1" />
              اللغة
            </div>
          </button>
        </div>
        
        <div className="p-6">
          <form onSubmit={handleSubmit}>
            {activeTab === 'account' && (
              <div className="space-y-6">
                <h3 className="text-lg font-medium text-gray-900">إعدادات الحساب</h3>
                
                <div className="border-t border-gray-200 pt-4">
                  <div className="grid grid-cols-1 gap-y-6 sm:grid-cols-2 sm:gap-x-4">
                    <div>
                      <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                        البريد الإلكتروني
                      </label>
                      <input
                        type="email"
                        name="email"
                        id="email"
                        value={accountSettings.email}
                        onChange={handleSettingChange}
                        className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                      />
                    </div>
                    
                    <div>
                      <label htmlFor="password" className="block text-sm font-medium text-gray-700">
                        كلمة المرور
                      </label>
                      <div className="mt-1 flex">
                        <input
                          type="password"
                          name="password"
                          id="password"
                          disabled
                          value="***************"
                          className="block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 bg-gray-50 sm:text-sm"
                        />
                        <button
                          type="button"
                          className="mr-2 inline-flex items-center px-3 py-2 border border-gray-300 shadow-sm text-sm leading-4 font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                        >
                          تغيير
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                
                <div className="border-t border-gray-200 pt-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h4 className="text-sm font-medium text-gray-900">الملف الشخصي العام</h4>
                      <p className="text-sm text-gray-500">
                        السماح للآخرين برؤية ملفك الشخصي والتواصل معك
                      </p>
                    </div>
                    <div className="flex items-center">
                      <label className="inline-flex relative items-center cursor-pointer">
                        <input
                          type="checkbox"
                          name="publicProfile"
                          checked={accountSettings.publicProfile}
                          onChange={handleSettingChange}
                          className="sr-only peer"
                        />
                        <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-indigo-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:right-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-indigo-600"></div>
                      </label>
                    </div>
                  </div>
                </div>
                
                <div className="border-t border-gray-200 pt-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h4 className="text-sm font-medium text-gray-900">النشرة الإخبارية</h4>
                      <p className="text-sm text-gray-500">
                        تلقي آخر الأخبار والتحديثات عن المنصة
                      </p>
                    </div>
                    <div className="flex items-center">
                      <label className="inline-flex relative items-center cursor-pointer">
                        <input
                          type="checkbox"
                          name="newsletter"
                          checked={accountSettings.newsletter}
                          onChange={handleSettingChange}
                          className="sr-only peer"
                        />
                        <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-indigo-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:right-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-indigo-600"></div>
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            )}
            
            {activeTab === 'security' && (
              <div className="space-y-6">
                <h3 className="text-lg font-medium text-gray-900">إعدادات الأمان</h3>
                
                <div className="border-t border-gray-200 pt-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h4 className="text-sm font-medium text-gray-900">المصادقة الثنائية</h4>
                      <p className="text-sm text-gray-500">
                        تفعيل المصادقة الثنائية لحماية حسابك بشكل أفضل
                      </p>
                    </div>
                    <div className="flex items-center">
                      <label className="inline-flex relative items-center cursor-pointer">
                        <input
                          type="checkbox"
                          name="twoFactorAuth"
                          checked={accountSettings.twoFactorAuth}
                          onChange={handleSettingChange}
                          className="sr-only peer"
                        />
                        <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-indigo-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:right-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-indigo-600"></div>
                      </label>
                    </div>
                  </div>
                </div>
                
                <div className="border-t border-gray-200 pt-4">
                  <h4 className="text-sm font-medium text-gray-900 mb-4">جلسات تسجيل الدخول النشطة</h4>
                  
                  <div className="bg-gray-50 rounded-lg p-4 mb-4">
                    <div className="flex justify-between items-center">
                      <div>
                        <p className="text-sm font-medium text-gray-900">الجهاز الحالي</p>
                        <p className="text-xs text-gray-500">Windows 10 - Chrome</p>
                        <p className="text-xs text-gray-500">الموقع: الرياض، المملكة العربية السعودية</p>
                      </div>
                      <div>
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                          نشط الآن
                        </span>
                      </div>
                    </div>
                  </div>
                  
                  <button type="button" className="text-sm text-red-600 font-medium hover:text-red-800">
                    تسجيل الخروج من جميع الأجهزة الأخرى
                  </button>
                </div>
              </div>
            )}
            
            {activeTab === 'notifications' && (
              <div className="space-y-6">
                <h3 className="text-lg font-medium text-gray-900">إعدادات الإشعارات</h3>
                
                <div className="border-t border-gray-200 pt-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h4 className="text-sm font-medium text-gray-900">إشعارات البريد الإلكتروني</h4>
                      <p className="text-sm text-gray-500">
                        تلقي إشعارات عبر البريد الإلكتروني
                      </p>
                    </div>
                    <div className="flex items-center">
                      <label className="inline-flex relative items-center cursor-pointer">
                        <input
                          type="checkbox"
                          name="notifications"
                          checked={accountSettings.notifications}
                          onChange={handleSettingChange}
                          className="sr-only peer"
                        />
                        <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-indigo-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:right-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-indigo-600"></div>
                      </label>
                    </div>
                  </div>
                </div>
                
                <div className="border-t border-gray-200 pt-4">
                  <h4 className="text-sm font-medium text-gray-900 mb-4">أنواع الإشعارات</h4>
                  
                  <div className="space-y-4">
                    <div className="flex items-center">
                      <input
                        id="notification-orders"
                        name="notification-orders"
                        type="checkbox"
                        className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
                        defaultChecked
                      />
                      <label htmlFor="notification-orders" className="mr-2 block text-sm text-gray-700">
                        طلبات الخدمة الجديدة
                      </label>
                    </div>
                    
                    <div className="flex items-center">
                      <input
                        id="notification-reviews"
                        name="notification-reviews"
                        type="checkbox"
                        className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
                        defaultChecked
                      />
                      <label htmlFor="notification-reviews" className="mr-2 block text-sm text-gray-700">
                        التقييمات الجديدة
                      </label>
                    </div>
                    
                    <div className="flex items-center">
                      <input
                        id="notification-messages"
                        name="notification-messages"
                        type="checkbox"
                        className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
                        defaultChecked
                      />
                      <label htmlFor="notification-messages" className="mr-2 block text-sm text-gray-700">
                        الرسائل الجديدة
                      </label>
                    </div>
                    
                    <div className="flex items-center">
                      <input
                        id="notification-promotions"
                        name="notification-promotions"
                        type="checkbox"
                        className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
                      />
                      <label htmlFor="notification-promotions" className="mr-2 block text-sm text-gray-700">
                        العروض والتخفيضات
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            )}
            
            {activeTab === 'appearance' && (
              <div className="space-y-6">
                <h3 className="text-lg font-medium text-gray-900">إعدادات المظهر</h3>
                
                <div className="border-t border-gray-200 pt-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h4 className="text-sm font-medium text-gray-900">الوضع الداكن</h4>
                      <p className="text-sm text-gray-500">
                        تفعيل المظهر الداكن للمنصة
                      </p>
                    </div>
                    <div className="flex items-center">
                      <label className="inline-flex relative items-center cursor-pointer">
                        <input
                          type="checkbox"
                          name="darkMode"
                          checked={accountSettings.darkMode}
                          onChange={handleSettingChange}
                          className="sr-only peer"
                        />
                        <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-indigo-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:right-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-indigo-600"></div>
                      </label>
                    </div>
                  </div>
                </div>
                
                <div className="border-t border-gray-200 pt-4">
                  <h4 className="text-sm font-medium text-gray-900 mb-4">حجم الخط</h4>
                  
                  <div className="flex items-center space-x-2 space-x-reverse">
                    <span className="text-xs text-gray-600">أصغر</span>
                    <input
                      type="range"
                      min="1"
                      max="5"
                      defaultValue="3"
                      className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer"
                    />
                    <span className="text-xs text-gray-600">أكبر</span>
                  </div>
                </div>
              </div>
            )}
            
            {activeTab === 'language' && (
              <div className="space-y-6">
                <h3 className="text-lg font-medium text-gray-900">إعدادات اللغة</h3>
                
                <div className="border-t border-gray-200 pt-4">
                  <label htmlFor="language" className="block text-sm font-medium text-gray-700">
                    لغة واجهة المستخدم
                  </label>
                  <select
                    id="language"
                    name="language"
                    value={accountSettings.language}
                    onChange={handleSettingChange}
                    className="mt-1 block w-full py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  >
                    <option value="ar">العربية</option>
                    <option value="en">الإنجليزية</option>
                    <option value="fr">الفرنسية</option>
                  </select>
                </div>
                
                <div className="border-t border-gray-200 pt-4">
                  <label htmlFor="dateFormat" className="block text-sm font-medium text-gray-700">
                    تنسيق التاريخ
                  </label>
                  <select
                    id="dateFormat"
                    name="dateFormat"
                    className="mt-1 block w-full py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                    defaultValue="dd/mm/yyyy"
                  >
                    <option value="dd/mm/yyyy">DD/MM/YYYY</option>
                    <option value="mm/dd/yyyy">MM/DD/YYYY</option>
                    <option value="yyyy/mm/dd">YYYY/MM/DD</option>
                  </select>
                </div>
              </div>
            )}
            
            <div className="flex justify-end mt-6 pt-6 border-t border-gray-200">
              <button type="submit" className="inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                حفظ الإعدادات
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
} 